
import React from 'react'
// eslint-disable-next-line
import style from '../css/style.css'
// eslint-disable-next-line
import { reduxForm, Field } from 'redux-form'
import {connect} from 'react-redux'
import {modificaBanca} from '../redux/actions'

class Teste extends React.Component {

    constructor(props){
        super(props);
        
        this.state = {
            banca:0,
        }
    }

    noReload(e){
        e.preventDefault(this)
    }

    render(){
        const { banca } = this.props;
        return(
            <form onSubmit={this.noReload}>
                
            <label> Ordem: R$ {banca}</label>
                <input
                    onChange={(e) => {this.setState({banca:e.target.value})} } 
                    /> 
                <button type="submit" onClick={ () => this.props.modificaBanca(this.state.banca) } >Submit</button>
            </form>
        );
    }
}


const mapStateToProps = state => (
    {
        banca : state.CalculosReducers.banca,
        resultado : state.CalculosReducers.resultado,
    }
)

export default connect(mapStateToProps, {
    modificaBanca
})(Teste); 