import { 
    MODIFICA_BANCA,
    MODIFICA_PAYOUT,
    MODIFICA_RETORNO,
    MODIFICA_STOPLOSS,
    MODIFICA_ORDEM
 } from '../actions'

const INITIAL_STATE = {
    banca:0,
    payout:0,
    stopLoss:0,
    ordem:0,
    ordem2:0,
    ordem3:0,
    ordem4:0,
    ordem5:0,
    retorno:0,
    retorno2:0,
    retorno3:0,
    retorno4:0,
    retorno5:0,
    resultado: 0,
}

const CalculosReducers = (state = INITIAL_STATE, action) => {
    switch(action.type){        
        case MODIFICA_BANCA:
            return {
                ...state,
                banca: action.payload
            }
        case MODIFICA_PAYOUT:
            return {
                ...state,
                payout: action.payload
            }
        case MODIFICA_RETORNO:
            return {
                ...state,
                retorno: action.payload
            }    
        case MODIFICA_STOPLOSS:
            return {
                ...state,
                stopLoss: action.payload
            }
        case MODIFICA_ORDEM:
            return {
                ...state,
                ordem: action.payload,
            }         
        }
        
    return state;
}
 
export default CalculosReducers;