
import React from 'react';
// eslint-disable-next-line
import style from '../css/style.css';
import {connect} from 'react-redux';
import { 
    modificaBanca,
    modificaOrdem,
    modificaPayout,
    modificaRetorno,
    modificaStopLoss,
 } from '../redux/actions';
 import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";

  
class Principal extends React.Component {
    render(){
        return (        
        <div>   
            <Router>
                <div>
                    <Link to="/">Soros</Link>
                    <Link to="/martingale">Martingale</Link>
                    <Link to="/sorosGale">SorosGale</Link>

                    <hr />
                    <Switch>
                        <Route exact path="/">
                            <Principal />
                        </Route>
                        <Route path="/martingale">
                            <Martingale />
                        </Route>
                        <Route path="/sorosGale">
                            <SorosGale />
                        </Route>
                    </Switch>
                </div>
            </Router>
        </div>
        );
    }
  }

class Martingale extends React.Component {
    render(){
        return (
            <div>
                <Router>
                    <Link to="/sorosGale">SorosGale</Link>
                    <Switch>
                        <Route path="/sorosGale">
                            <SorosGale />
                        </Route>
                    </Switch>

                    </Router>
            </div>
        );
    }
}

class SorosGale extends React.Component {
    render(){
        return (
            <div>
                <Router>
                    <Link to="/martingale">Martingale</Link>                    
                    <Switch>
                        <Route path="/martingale">
                            <Martingale />
                        </Route>
                    </Switch>
                </Router>
            </div>
        );
  }
}

class TabelaCalculos extends React.Component {
    constructor(props){
        super(props);        
        this.state = {
            stopLoss: props.stopLoss,
            payout:98,
            QtdaMartingale:0,
            ordem: 50,
            ordem2: props.ordem2,
            ordem3: props.ordem3,
            ordem4: props.ordem4,
            ordem5: props.ordem5,
            retorno: props.retorno,
            retorno2: props.retorn2,
            retorno3: props.retorn3,
            retorno4: props.retorn4,
            retorno5: props.retorno5,
        }
        this.noReload = this.noReload.bind(this);
        this.ordem2 = this.ordem2.bind(this);
        this.ordem3 = this.ordem3.bind(this);
        this.ordem4 = this.ordem4.bind(this);
        this.ordem5 = this.ordem5.bind(this);

        this.retorno = this.retorno.bind(this);
        this.retorno2 = this.retorno2.bind(this);
        this.retorno3 = this.retorno3.bind(this);
        this.retorno4 = this.retorno4.bind(this);
        this.retorno5 = this.retorno5.bind(this);
        this.zerar = this.zerar.bind(this);
    }
    noReload(e){
        e.preventDefault(this);
    }
    zerar(){
        this.setState({
            payout: this.state.payout * 0,
            ordem: this.state.ordem * 0 ,
        })
    }
    retorno(){  
        let temp = this.state.ordem * (this.state.payout/100);
        return temp;
    }
    retorno2(){
        let temp = parseFloat(this.ordem2()) * (this.state.payout/100);
        var calculo = temp                
        var numero = calculo+"";
        var decimal = numero.indexOf(".");
        if (decimal !== -1) {
            var calculo = calculo.toFixed(2);
        }
        var calculo = parseFloat(calculo);
        return calculo;
    }
    retorno3(){  
        let temp = parseFloat(this.ordem3()) * (this.state.payout/100);
        var calculo = temp                
        var numero = calculo+"";
        var decimal = numero.indexOf(".");
        if (decimal !== -1) {
            var calculo = calculo.toFixed(2);
        }
        var calculo = parseFloat(calculo);
        return calculo;
    }
    retorno4(){  
        let temp = parseFloat(this.ordem4()) * (this.state.payout/100);
        var calculo = temp                
        var numero = calculo+"";
        var decimal = numero.indexOf(".");
        if (decimal !== -1) {
            var calculo = calculo.toFixed(2);
        }
        var calculo = parseFloat(calculo);
        return calculo;
    }
    retorno5(){  
        let temp = parseFloat(this.ordem5()) * (this.state.payout/100);
        var calculo = temp                
        var numero = calculo+"";
        var decimal = numero.indexOf(".");
        if (decimal !== -1) {
            var calculo = calculo.toFixed(2);
        }
        var calculo = parseFloat(calculo);
        return calculo;
    }
    ordem2(){
        let temp = parseFloat(this.state.ordem) + parseFloat(this.state.ordem * this.state.payout) / 100;
        var calculo = temp                
        var numero = calculo+"";
        var decimal = numero.indexOf(".");
        if (decimal !== -1) {
            var calculo = calculo.toFixed(2);
        }
        var calculo = parseFloat(calculo);
        return calculo;
    }
    ordem3(){
        let ordem = parseFloat(this.state.ordem) + parseFloat(this.state.ordem * this.state.payout) / 100;
        let retorno = parseFloat(ordem) * (this.state.payout/100);
        var calculo = ordem + retorno;                
        var numero = calculo+"";
        var decimal = numero.indexOf(".");
        if (decimal !== -1) {
            var calculo = calculo.toFixed(2);
        }
        var calculo = parseFloat(calculo);
        return calculo;
    }
    ordem4(){  
        
        let ordem = this.ordem3();
        let retorno = this.retorno3();
        var calculo = ordem + retorno;                
        var numero = calculo+"";
        var decimal = numero.indexOf(".");
        if (decimal !== -1) {
            var calculo = calculo.toFixed(2);
        }
        var calculo = parseFloat(calculo);
        return calculo;
    }
    ordem5(){  
        let ordem = this.ordem4();
        let retorno = this.retorno4();
        var calculo = ordem + retorno;                
        var numero = calculo+"";
        var decimal = numero.indexOf(".");
        if (decimal !== -1) {
            var calculo = calculo.toFixed(2);
        }
        var calculo = parseFloat(calculo);
        return calculo;
    }    
    render(){
        return (
            <div>                
                <form onSubmit={this.noReload}> 
                <select>
                    <option>Soros</option>
                </select>
                    <table>
                        <tr>
                            <td>
                                <h3>Informe o valor de sua ordem:</h3>
                                <input
                                    value={ this.state.ordem }
                                    type="number" 
                                    onChange={(e) => {this.setState({ordem:e.target.value})} } />
                                <h4> Ordem: R${ this.state.ordem } </h4>                                
                            </td>
                            <td>
                                <h3>Informe o valor do payout:</h3>
                                <input 
                                    value={ this.state.payout }
                                    type="number" 
                                    onChange={(e) => {this.setState({payout:e.target.value})} } />
                                <h4> Payout: {this.state.payout }% </h4>                            
                            </td>
                        </tr>
                    </table>
                    
                    <h3>Resultado:</h3>

                    <table class="tableResultado">
                        <tr>
                            <td>
                                
                            </td>
                            <td>
                                1° Ordem
                            </td>
                            <td>
                                Retorno
                            </td>
                        </tr>
                        <tr>
                            <td>
                                1° Nível
                            </td>
                            <td>
                                R${ this.state.ordem }
                            </td>
                            <td>
                                R${ this.retorno() }
                            </td>
                        </tr>
                        <tr>
                            <td>
                                2° Nível
                            </td>
                            <td>
                                R${ this.ordem2() }
                            </td>
                            <td>
                                R${ this.retorno2() }
                            </td>
                        </tr>
                        <tr>
                            <td>
                                3° Nível
                            </td>
                            <td>
                                R${ this.ordem3() }
                            </td>
                            <td>
                                R${ this.retorno3() }
                            </td>
                        </tr>
                        <tr>
                            <td>
                                4° Nível
                            </td>
                            <td>
                                R${ this.ordem4() }
                            </td>
                            <td>
                                R${ this.retorno4() }
                            </td>
                        </tr>
                        <tr>
                            <td>
                                5° Nível
                            </td>
                            <td>
                                R${ this.ordem5() }
                            </td>
                            <td>
                                R${ this.retorno5() }
                            </td>
                        </tr>
                    </table>

                    <button type="button" onClick={this.zerar} >Zerar</button>

                </form>
                
            </div>            
        );
    }
    
}


const mapStateToProps = state => (
    {
        banca : state.CalculosReducers.banca,
        stopLoss : state.CalculosReducers.stopLoss,
        payout : state.CalculosReducers.payout,        
        ordem : state.CalculosReducers.ordem,
        ordem2 : state.CalculosReducers.ordem2,
        ordem3 : state.CalculosReducers.ordem3,
        ordem4 : state.CalculosReducers.ordem4,
        ordem5 : state.CalculosReducers.ordem5,
        retorno : state.CalculosReducers.retorno,
        retorno2 : state.CalculosReducers.retorno2,
        retorno3 : state.CalculosReducers.retorno3,
        retorno4 : state.CalculosReducers.retorno4,
        retorno5 : state.CalculosReducers.retorno5,
    }
)

export default connect(mapStateToProps, {
    modificaBanca,
    modificaOrdem,
    modificaPayout,
    modificaRetorno,
    modificaStopLoss,
})(TabelaCalculos); 